.autoCard {
    background-color: #f5f5f5;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
}
.cardTitle {
    display: block;
    font-size: 1.17em;
    font-weight: bold;
    padding: 0;
    margin: 0;
}
.cardImage {
    width: 100%;
    flex: 1 1 auto;
    max-height: 200px;
}

.description {
    text-align: left;
    width: 100%;
    padding: 1px 10px 10px
}

.description > *:not(:last-child) {
    margin-bottom: 5px;
}

img {
    border-radius: 20px;
    width: 100%;
    object-fit: cover;
    object-position: center;
    max-height: 200px;
}

.buttonCard {
    cursor: pointer;
    width: 100%;
    background-color: white;
    display: inline-block;
    padding: 5px;
    border: none;
    border-radius: 20px;
    text-align: center;
    color: inherit;
    text-decoration: none;
}

.price {
    font-family: Arial, sans-serif;
}